.topbar{
    position: fixed;
    top: 0px;
    left: 0px;
    height: 16vh;
    width: 100%;
    border: 5px solid rgb(0, 5, 32);
    border-bottom-style: darkblue;
    display: flex;
    flex-direction: row;
    background-color: rgb(0, 0, 43);
}
.base{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-29%);
    width: 100%;
    height: 70%;
    display: flex;
    flex-direction: row;
}
.tower{
    display: block;
    background-color: powderblue;
    flex: 1;
    display: flex;
    flex-direction: column-reverse;
    border: 1px solid grey;
    margin: 0px;
    padding: 0px;
}
.disk1{
    display: block;
    background-color: red;
    align-self: center;
    align-content: flex-end;
    width: 20%;
    height: 8%;
    margin: 0px;
    border-radius: 15px;
    border: 1px solid black;
}
