.wood{
    border: 1px solid black;
    width: 100%;
    height: 6%;
    margin: 0px;
    display: block;
    background-color: maroon;
}
.restart{
    position: fixed;
    left: 1%;
    top: 3%;
    font-size: 6vh;
    color: rgb(0, 31, 122);
    background-color: rgb(255, 166, 0);
    height: 12%;
    width: 20%;
}
.restart :hover{
    background-color: rgb(255, 115, 0);
}
.slider{
    position: fixed;
    left: 25%;
    top: 3%;
    outline: none;
    opacity: 1;
    transition: 0.1s;
    border-radius: 3px;
    cursor: pointer;
    width: 32vh;
    height: 4%;
}
.num{
    position: absolute;
    left: 26.2%;
    top: 35%;
    color: rgb(255, 166, 0);
    font-size: 6.5vh;
    align-self: center;
    padding: 0px;
    margin: 0px;
}
.moves{
    position: absolute;
    left: 46%;
    top: 3%;
    color: rgb(255, 166, 0);
    font-size: 6vh;
    font-size-adjust: 3vw;
    flex: 1;
    align-self: center;
    padding: 0px;
}
.nummove{
    position: absolute;
    left: 50%;
    top: 45%;
    font-size: 6vh;
    color: rgb(255, 166, 0);
    padding: 0px;
    flex: 1;
}
.showTime{
    position: fixed;
    left: 64%;
    top: 1.5%;
    font-size: 6vh;
    color: rgb(255, 166, 0);
}
.howToPlay{
    position: fixed;
    right: 2%;
    top: 3%;
    font-size: 4vh;
    color: rgb(9, 0, 138);
    background-color: rgb(255, 166, 0);
    height: 12%;
    width: 20%;
}
.howToPlay :hover{
    background-color: rgb(255, 115, 0);
    color: rgb(7, 1, 92);
}
.conf{
    width: 100%;
    height: 100%;
}
.help{
    top: 50%;
    left: 50%;
    transform: translate(-50%,-60%);
    width: 50%;
    height: 100%;
    margin: 0px;
    padding: 0px;
    background-color: rgb(255, 235, 197);
    border: 3px solid rgb(243, 196, 110);
    display: flex;
    flex-direction: column;
    position: fixed;
}